@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --ScissorsGradient: hsl(39, 89%, 49%);
  --toScissorsGradient: hsl(40, 84%, 53%);
  --PaperGradient: hsl(230, 89%, 62%);
  --toPaperGradient: hsl(230, 89%, 65%);
  --RockGradient: hsl(349, 71%, 52%);
  --toRockGradient: hsl(349, 70%, 56%);
  --LizardGradient: hsl(261, 73%, 60%);
  --toLizardGradient: hsl(261, 72%, 63%);
  --Cyan: hsl(189, 59%, 53%);
  --toCyan: hsl(189, 58%, 57%);
  --DarkText: hsl(229, 25%, 31%);
  --ScoreText: hsl(229, 64%, 46%);
  --HeaderOutline: hsl(217, 16%, 45%);
  --RadialGradient: hsl(214, 47%, 23%);
  --toRadialGradiant: hsl(237, 49%, 15%);
}

html {
  font-family: "Barlow Semi Condensed", sans-serif;
}

body {
  background-image: radial-gradient(
    var(--RadialGradient),
    var(--toRadialGradiant)
  );
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  height: 100vh;
  animation: onload ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.App {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.top-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #fff;
  border-radius: 9px;
  padding: 9px;
  margin: 3rem;
  width: 500px;
  cursor: pointer;
}
.title-container {
  line-height: 1;
}
.score-container {
  background-color: #fff;
  color: #000;
  padding: 4% 8%;
  border-radius: 4px;
  text-align: center;
}
p {
  color: #aea8a8;
  letter-spacing: 2px;
}

.score-container h1 {
  font-size: 3rem;
}
h1 {
  transition: color ease-in 0.22s;
}
.game-container {
  background-image: url("./image/bg-triangle.svg");
  background-position: center;
  background-repeat: no-repeat;
  margin: 3rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  height: 300px;
}

.paper-container {
  grid-area: 1 / 1 / 2 / 2;
  background-color: #fff;
  border: 17px solid var(--PaperGradient);
  padding: 19px;
  border-radius: 50%;
  box-shadow: inset -2px 2px 0px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition: box-shadow ease 0.22s;
}

.scissor-container {
  grid-area: 1 / 3 / 2 / 4;
  background-color: #fff;
  border: 17px solid var(--ScissorsGradient);
  padding: 19px;
  border-radius: 50%;
  box-shadow: inset -2px 2px 0px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition: box-shadow ease 0.22s;
}

.rock-container {
  grid-area: 3 / 2 / 4 / 3;
  background-color: #fff;
  border: 17px solid var(--RockGradient);
  padding: 25px;
  border-radius: 50%;
  box-shadow: inset -2px 2px 0px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition: box-shadow ease 0.22s;
}

button {
  background-color: var(--toRadialGradiant);
  width: 100px;
  padding: 6px 2px;
  color: #ebebeb;
  border: 1px solid #cfcdcd;
  border-radius: 4px;
  cursor: pointer;
  position: fixed;
  bottom: 50px;
  right: 50px;
  transition: opacity ease 0.22s;
  text-transform: uppercase;
  letter-spacing: 1.6px;
  font-family: "Barlow Semi Condensed", sans-serif;
  font-weight: 600;
}

button:hover {
  opacity: 0.4;
}

.rock-container:hover {
  box-shadow: -1px 1px 11px 0px #fff;
}

.paper-container:hover {
  box-shadow: -1px 1px 11px 0px #fff;
}

.scissor-container:hover {
  box-shadow: -1px 1px 11px 0px #fff;
}

h1:hover {
  color: #c2b8b8;
}

.select-container {
  display: flex;
  justify-content: center;
  margin-top: 5rem;
}
.player-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  padding: 25px;
  border-radius: 50%;
  box-shadow: inset -2px 2px 0px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  width: 170px;
  height: 170px;
  margin: 20px;
}

.computer-conntainer img {
  width: 80px;
  height: 80px;
}
.player-container img {
  width: 80px;
  height: 80px;
}
.computer-conntainer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  padding: 25px;
  border-radius: 50%;
  box-shadow: inset -2px 2px 0px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  width: 170px;
  height: 170px;
  margin: 20px;
}

.not-selected {
  background-color: #020000;
  padding: 25px;
  border-radius: 50%;
  cursor: pointer;
  width: 180px;
  height: 180px;
  margin: 30px;
  opacity: 0.1;
}

.Player-win,
.House-win {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  margin-top: 1rem;
}

.player-picked,
.house-picked {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  padding: 25px;
  border-radius: 50%;
  box-shadow: inset -2px 2px 0px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  width: 200px;
  height: 200px;
  margin: 30px;
}
.player-win,
.house-win {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 8px;
}

.player-win-effect {
  box-shadow: 0 0 rgba(210, 203, 203, 0.2), 0 0 0 40px rgba(186, 180, 180, 0.2),
    0 0 0 80px rgba(199, 192, 192, 0.2), 0 0 0 120px rgba(180, 174, 174, 0.2);

  animation: win 3s linear infinite;
  z-index: -1;
}
.house-win-effect {
  box-shadow: 0 0 rgba(210, 203, 203, 0.2), 0 0 0 40px rgba(186, 180, 180, 0.2),
    0 0 0 80px rgba(199, 192, 192, 0.2), 0 0 0 120px rgba(180, 174, 174, 0.2);

  animation: win 3s linear infinite;
  z-index: -1;
}
.btn {
  background-color: #fff;
  color: #000;
  position: relative;
  top: 0;
  left: 0;
  margin: 10px;
  width: 150px;
  padding: 8px;
}

.btn:hover {
  color: rgb(160, 72, 4);
  opacity: 1;
}

.Player-container,
.House-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.Player-container h2,
.House-container h2 {
  margin-bottom: 3rem;
}
@keyframes onload {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@keyframes win {
  to {
    box-shadow: 0 0 0 40px rgba(182, 173, 173, 0.2),
      0 0 0 80px rgba(178, 171, 171, 0.2), 0 0 0 120px rgba(181, 177, 177, 0.2),
      0 0 0 160px rgba(174, 168, 168, 0);
  }
}

@media screen and (min-width: 386px) and (max-width: 566px) {
  .top-container {
    width: 349px;
  }

  button {
    position: fixed;
    bottom: 30px;
    left: 40%;
  }
  .Player-win,
  .House-win {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
  }
  .player-win,
  .house-win {
    grid-column: span 2;
    grid-row: 2;
    padding: 0;
  }

  .player-picked,
  .house-picked {
    display: flex;
    flex-wrap: wrap;
    background-color: #fff;
    padding: 25px;
    border-radius: 50%;
    box-shadow: inset -2px 2px 0px 2px rgba(0, 0, 0, 0.3);
    cursor: pointer;
    width: 150px;
    height: 150px;
    margin: 30px;
  }

  .player-win-effect {
    box-shadow: 0 0 rgba(210, 203, 203, 0.2),
      0 0 0 40px rgba(186, 180, 180, 0.2), 0 0 0 80px rgba(199, 192, 192, 0.2),
      0 0 0 120px rgba(180, 174, 174, 0.2);

    animation: win 3s linear infinite;
    z-index: -1;
  }
  .house-win-effect {
    box-shadow: 0 0 rgba(210, 203, 203, 0.2),
      0 0 0 40px rgba(186, 180, 180, 0.2), 0 0 0 80px rgba(199, 192, 192, 0.2),
      0 0 0 120px rgba(180, 174, 174, 0.2);

    animation: win 3s linear infinite;
    z-index: -1;
  }

  @keyframes onload {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }
  @keyframes win {
    to {
      box-shadow: 0 0 0 40px rgba(182, 173, 173, 0.2),
        0 0 0 80px rgba(178, 171, 171, 0.2),
        0 0 0 120px rgba(181, 177, 177, 0.2), 0 0 0 160px rgba(174, 168, 168, 0);
    }
  }
}
